.projectHeader {
    background: radial-gradient(rgba(118, 119, 82, 0.698), #4e4848fa);
    border-radius: 20px;
    padding: 10px 0;
}

.projectCard {
    background: radial-gradient(rgba(175, 177, 105, 0.726), rgba(78, 72, 72, 0.883));
    box-shadow: 20px 30px 15px 15px rgba(0,0,0,0.4);
    z-index: 2;
    border-radius: 40px;
    
}

#projectWrap{
    min-height: 2000px;
}
#project1 {
    position: absolute;
    left: 0%;
    top: 9%;
    z-index: 5;
} 

#project2 {
    position: absolute;
    left: 10%;
    top: 9%;
    z-index: 4;
} 

#project3 {
    position: absolute;
    left: 20%;
    top: 9%;
    z-index: 3;
} 

#project1:hover, #project2:hover, #project3:hover {
    z-index: 5;
    transform: translateY(-150px);
    transition: all .4s ease-in-out;
    background: radial-gradient(rgb(175, 177, 105), rgb(78, 72, 72));
}
#project3:hover ~ #project1 {
    display: none;
}

#project3:hover ~ #project2 {
    display: none;
}
  .certContainer {
    width: 100%;
    min-height: 900px;
    background: radial-gradient(rgba(114, 116, 68, 0.575), rgba(61, 54, 54, 0.678));
    border-radius: 50px;
  }
  #image1 {
    perspective: 3000px;
    width: 80%;
    position: absolute;
    left: 12%;
    top: 4%;
    transform: translate(-50%, -50%);
    transform-style: preserve-3d;
  }

  #image2 {
    perspective: 3000px;
    width: 80%;
    position: absolute;
    left: 12%;
    top: 15%;
    margin-top: 50px;
    transform: translate(-50%, -50%);
    transform-style: preserve-3d;
  }

  #image1, #image2 {
    transform: rotateX(70deg) rotateZ(-60deg) translate3d(-120px, 0px, 70px);
    box-shadow: -80px 60px 15px 5px rgba(0,0,0,0.4);
    transition: all .4s;
    transform-style: preserve-3d;
  }
  #image1:hover {
    transform: rotateX(0deg) rotateZ(0deg) translate3d(0px, 0px, 0px);
    box-shadow: 0px 0px 0px 0px rgba(0,0,0,0.0);
    top: 6%;
  }

  #image2:hover {
    transform: rotateX(0deg) rotateZ(0deg) translate3d(0px, 0px, 0px);
    box-shadow: 0px 0px 0px 0px rgba(0,0,0,0.0);
    top: 0%;
  }
  

  #image1:hover ~ #image2 {
    display: none;
  }

